.chat__container {
    padding-top: 40px;
    display: grid;
    gap: 50px;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 90px 1fr;
    grid-template-areas:
        "top top"
        "left right";
}

.chat__header {
    grid-area: top;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.left-side {
    grid-area: left;
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.right-side {
    grid-area: right;
}

.no-chat {
    border: 2px solid var(--black);
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
}